$bg-header: #666666;
$bg-body: $bg-header;
$fg-body: #303030;

$fg-link: $fg-body - #101010 + #000060;
$fg-link-active: $fg-link + #606060;
$fg-link-active-shadow:$fg-link-active+#FFFFFF;


$bg-index: $bg-body + #454545;
$fg-index: #000000; // Really only border
$fg-yeardate: $fg-body;
$fg-postdate: $fg-body;

$bg-article: $bg-index;
$fg-article: $fg-body;

$bg-navigate: $bg-index;
$fg-navigate: $fg-link;

$bg-footer: $bg-index;
$fg-footer: $fg-index;


$bg-block: $bg-index - #101010;
$bg-mathjax: $bg-block;
$bg-code:  $bg-block;
$fg-code: $fg-body;


$bg-thead: $bg-block;

@mixin bodyresp() {
// First size
@media only screen and (max-width: 480px)  { width: 100%; }
//second size
@media only screen and (max-width: 1024px) and
(min-width:481px)  { width: 95%; }
@media only screen and (min-width: 1025px)  { width: 1300px; }
}
@mixin fontSetup($scale,$scalel:1) {
@media only screen and (max-width: 480px)  {
	font-size: calc(20px * #{$scale} );
	line-height: calc(20px  * #{$scalel} );
}
@media only screen and (max-width: 1024px) and
(min-width:481px)  { 
	font-size: calc(25px * #{$scale} );
	line-height: calc(25px * #{$scalel} );
}
@media only screen and (min-width: 1025px)  { 
	font-size: calc(35px * #{$scale} );
	line-height: calc(35px  * #{$scalel} );
}

}

@font-face{
  font-family:ply;
  src:url(/assets/wr.woff2)
}

@font-face {
	font-family:codefont;
	src:url(/assets/nvibm.woff2)
}

.MathJax_Display {
	padding: 1% 0px 1% 0px;
	border: 2px solid;
	background: $bg-mathjax;
}


code,
pre,
.code,
.footnotes {
  font-family: codefont;
}

/* Code outside a pre */
code {
  color: inherit;
  background: inherit;
  padding: 2px 6px;
  font-size: 13px;
  @include fontSetup(0.75);
}

/* Code in a pre-formatted code block */
pre {
  display: block;
  margin-top: 0;
  margin-bottom: 1rem;
  font-size: 0.8rem;
  line-height: 1.4;
  white-space: pre;
  overflow-x: auto;
	border: 1px solid;
	border-left: 15px solid;
	border-right: 15px solid;
  padding: 1rem;
	code {
		background-color:inherit;
		color: inherit;
	}
  @include fontSetup(0.5);
}

body {
  width: 90%;
  background-color: $bg-body;
  font-weight: normal;
  font-style: normal;
  font-family: ply;
	color: $fg-body;
  margin: auto;
  -webkit-text-size-adjust: 100%;
  padding: 10px 20px;
  @include bodyresp();
  @include fontSetup(1);
}

.page-navigation {
	display: block;
	margin: 0% 0% 1% 0%;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 1);
  background-color: $bg-navigate;
	color: $fg-navigate;
  border: 2px solid black;

	.leftMenu {
		float: left;
		max-width: 40%;
		border: 1px solid;
	}

	.rightMenu {
		max-width: 40%;
		float: right;
		border: 1px solid;
		text-align: right;
	}

  a {
		margin: 0px;
		padding: 0px;
		width:48%;
		max-width:48%;
  }

  a:hover {
    color: $fg-link-active;
    border-color: #369;
		text-shadow: 0px 0px 3px $fg-link-active-shadow;
  }

	@include fontSetup(1);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    -webkit-margin-before: 50px;
    -webkit-margin-after: 10px;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    border-bottom: 1px solid black;
}
h1 { @include fontSetup(1.5, 1.6); }
h2 { @include fontSetup(1.4, 1.5); }
h3 { @include fontSetup(1.3, 1.4); }
h4 { @include fontSetup(1.2, 1.3); }
h5 { @include fontSetup(1.1, 1.2); }
h6 { @include fontSetup(1.0, 1.1); }
img {
  display: block;
  max-width: 100%;
  border: 2px solid black;
}
a {
  color: inherit;
  text-decoration: none;
  &.image {
    display: block;
    text-align: center;
    border: 0;
    border-radius: 5px;
    margin: 30px 0;
  }
}
mark {
  background: $bg-block;
  padding: 0;
}
blockquote {
  border: 1px solid black;
  border-left: 16px solid;
  margin: 40px 0;
  padding: 5px 30px;
  background: $bg-block;
	color: inherit;
}
.logo {
  box-shadow: 2px 2px 15px 2px rgba(0, 0, 0, 1);
  border: 1px solid #000000;
  position: relative;
  margin: 0 auto 30px;
  text-align: center;
}

iframe {
  margin: 45px 0 !important;
  border: 5px dotted black;
}
ul,
ol {
  margin: 20px 0;
  padding-left: 50px;
  li {
    margin: 10px 0;
    word-wrap: break-word;
  }
  img {
    margin: 40px 0;
    border-radius: 5px;
  }
}

sup {
  vertical-align: top;
  position: relative;
	left: -0.55em;
	top: -0.5em;
}


table {
  border: 1px solid #000000;
  width: 100%;
  table-layout: fixed;
  margin: 45px 0;
  thead {
    background: $bg-thead;
  }
  th {
    text-align: left;
    padding: 1% 1%;
    border-bottom: 5px solid #000;
  }
  td {
    border : 1px solid black;
    padding : 10px;
    @include fontSetup(0.75, 0.75);
  }
}

.divider {
    background: #222;
    background: -webkit-gradient(linear,left top,right top,from(rgba(255,255,255,0)),color-stop(#200),to(rgba(255,255,255,0)));
    background: -webkit-linear-gradient(left,rgba(255,255,255,0),rgba(255,255,255,255),rgba(255,255,255,0));
    background: linear-gradient(to right,rgba(255,255,255,0),rgba(20,20,20,255),rgba(20,20,20,0));
    height: 5px;
    margin: 0.5em 0;
}

iframe {
  border-top: 10px solid #C00000;
  border-bottom: 10px solid #C00000;
  width: 100%;
}
article {
  background-color: $bg-article;
	color: $fg-article;
  border: 2px solid #000000;
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 1);
  max-width: 100%;
	margin: 0;
	padding: 5%;
  .title {
    @include fontSetup(1.5,2);
    text-align: center;
  }
  .center {
    text-align: center;
    margin: 0 auto;
  }
  hr {
    border: 0;
    border-bottom: 1px solid #000;
    margin: 30px 0;
  }
  p {
    @include fontSetup(1);
    word-wrap: break-word;
  }
	// This is reference superscript
  .footnote {
    color: $fg-link;
    background-color: $bg-footer;
    @include fontSetup(0.75);
    margin: 0px;
  }
  .footnotes {
    @include fontSetup(0.10);
    margin: 50px auto;
    display: block;
    p {
      font-weight: normal;
    }
    ol {
      margin: 0px;
      padding-left: 15px;
      li {
        font-weight: bold;
      }
    }
    .reversefootnote {
      border: 0;
      color: #0087BE;
    }
  }
	a {
		color: $fg-link;
	}
  a:hover {
    color: $fg-link-active;
    border-color: #369;
		text-shadow: 0px 0px 1px $fg-link-active-shadow;
  }
	
}

.block {
  display: block;
}

.footer {
  background-color: $bg-footer;
  color: $fg-footer;
  max-width: 500px;
  text-align: center;
  margin: 0 auto;
  margin-top:5%;
  border: 2px solid #000;
  max-width: 60%;
  width: auto;
  padding: 1%;
  a {
    border: none;
    font-weight: bold;
  }
  @include fontSetup(0.75,1);
}

section {
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 1);
  background-color: $bg-index;
	color: $fg-index;
  border: 2px solid;
  max-width: 100%;
  margin: 0;
	h4 {
		color: $fg-yeardate;
		text-align: center;
		padding: 0px;
		margin: 1%;
	}
  ul {
		margin: 0% 5% 2% 5%;
		padding-top: 1%;
	  border-left: 5px solid;
		border-top: 5px solid;
    list-style: none;
		color: $bg-index;
    li {
      margin: 0% 0;
			padding: 0%; 
      .title {
        max-width: 100%;
        word-wrap: normal !important;
      }
      a {
        text-decoration: none;
        border: 0;
        color: $fg-link;
        box-sizing: border-box;
      }
      .post-date {
        float: right;
        margin: 0 0 0 1%px;
        color: $fg-postdate;
      }
      span {
        display: inline-block;
        letter-spacing: 1px;
        text-transform: uppercase;
      }
      @media (min-width: 400px) {
				a {
          &:hover {
            font-size: 125%;
						text-shadow: 0px 0px 41px $fg-link-active-shadow;
          }
        }
      }
    }
  }
}
@keyframes bounce {0% {
    transform: translate3d( 0, -1000px, 0 );
  }
  60% {
    transform: translate3d( 0, 25px, 0 );
  }
  75% {
    transform: translate3d( 0, -10px, 0 );
  }
  90% {
    transform: translate3d( 0, 5px, 0 );
  }
  100% {
    transform: none;
  }
}
@-webkit-keyframes bounce {0% {
    -webkit-transform: translate3d( 0, -1000px, 0 );
  }
  60% {
    -webkit-transform: translate3d( 0, 25px, 0 );
  }
  75% {
    -webkit-transform: translate3d( 0, -10px, 0 );
  }
  90% {
    -webkit-transform: translate3d( 0, 5px, 0 );
  }
  100% {
    -webkit-transform: none;
  }
}
